.Share-title {
  padding-bottom: 10px; }

.Share-action {
  display: table; }

.Share-select {
  display: table-cell;
  width: 100%;
  padding-right: 5px;
  vertical-align: top; }

.Share-ReactButton {
  display: table-cell;
  vertical-align: top; }

.Share-ReactDropdown {
  line-height: 1.3;
  cursor: pointer;
  padding: 9px 13px;
  font-size: 14px;
  display: block;
  border-radius: 4px;
  width: 100%; }



