@-webkit-keyframes ReactPlaceholder_shimmer {
  from {
    background-position: -1000px 0; }
  to {
    background-position: 1000px 0; } }

@keyframes ReactPlaceholder_shimmer {
  from {
    background-position: -1000px 0; }
  to {
    background-position: 1000px 0; } }

.ReactPlaceholder {
  height: 100%;
  width: 100%;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: ReactPlaceholder_shimmer;
          animation-name: ReactPlaceholder_shimmer;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background: #ebebeb;
  background: -webkit-gradient(linear, left top, right top, from(#ebebeb), color-stop(20%, #e0e0e0), color-stop(40%, #ebebeb));
  background: -webkit-linear-gradient(left, #ebebeb 0%, #e0e0e0 20%, #ebebeb 40%);
  background: -o-linear-gradient(left, #ebebeb 0%, #e0e0e0 20%, #ebebeb 40%);
  background: linear-gradient(to right, #ebebeb 0%, #e0e0e0 20%, #ebebeb 40%);
  background-size: 2000px 2000px;
  position: relative; }



